/* small mobile :320px. */
@media (max-width: 767px) {
    #hero-banner{
       background-image: none;
    }
    #hero-banner .hero__article-block .article__block--title {
       font-size: 2rem;
       font-weight: bold;
       text-transform: uppercase;
       line-height: 1.3;
       margin-bottom: 10px;
       font-family: var(--font-poppins);
    }
   .courses {
       display: -ms-grid;
       display: grid;
       grid-template-columns: repeat(1, 1fr) !important;
   }
   #hero-banner .hero__article-short-course-list{
    margin: 1rem 0 0 0;
   }
   .swiper-slide .slider-content p {
    font-size: .8rem !important;
    font-family: var(--font-poppins);
    font-weight: 700;
    text-transform: capitalize;
    color: #fff;
   }
   #short-info-counter {
      display: none !important;
   }
   #banner-section .banner-bg .banner-title{
      width: 100%;
   }
 }
  
